/* color aliases */
:root {
  --text-color: #333;
  --secondary-text-color: #777581;
  --background-color: #466376;
  --border-color: #e99c2b;
  /* fun game card colors */
  --card-color-1: #f8d7da;
  --card-color-2: #d1ecf1;
  --card-color-3: #fff3cd;
  --card-color-4: #d4edda;
}

@font-face {
  font-family: "Space Mission";
  src: url("./assets/SpaceMission.otf") format("opentype");
}

@font-face {
  font-family: "Maldini Bold";
  src: url("./assets/MaldiniBold.ttf") format("truetype");
}
.App {
  text-align: center;
  background-color: var(--background-color);
}

.spm {
  /* bold and italics */
  font-family: "Maldini Bold" !important;
  font-size: 2.7em;
  /* increase letter spacing */
  letter-spacing: 0.1em;
}

.c_card {
  color: #472e09 !important;
}

.c_secondary {
  color: var(--secondary-text-color) !important;
}

.c_white {
  color: white !important;
}

.ff_md {
  font-family: "Maldini Bold" !important;
}

body {
  background-color: var(--background-color) !important;
}

.App-logo {
  height: 50px;
  pointer-events: none;
}

.App-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  /* use space mission font */
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.challenge_card {
  /* card for mobile such that 4 of them can be next to each other */
  width: 180px;
  height: 250px;
  /* bg changes on which card it is */
  background-color: var(--card-color-3);
  /* border radius */
  border-radius: 10px;
  /* shadow */
  border: 5px solid var(--border-color);

  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  /* margin */
  margin: 10px;
  padding: 10px;

  display: flex;
  flex-direction: column;
}

.f_lg {
  font-size: 1.5em !important;
}

.f_xl {
  font-size: 2em !important;
}

.f_xxl {
  font-size: 3em !important;
}

/* smaller line spacing */
.ls_sm {
  line-height: 1.2em !important;
}

.ls_ss {
  line-height: 1em !important;
}

.grid_container {
  /* scroll overflow horizontally */
  overflow-x: auto;
  display: flex;
}

.tr_counter {
  /* counter for timer */
  font-size: 1.5em;
  color: var(--secondary-text-color);
  top: 0;
  right: 10px;
  position: absolute;
}

.lg_w {
  width: 1000px;
  display: flex;
  flex-direction: row;
}

.mr_1 {
  margin-right: 8px !important;
}

.mr_2 {
  margin-right: 16px !important;
}

.mr_3 {
  margin-right: 24px !important;
}

.mr_4 {
  margin-right: 32px !important;
}

.mr_5 {
  margin-right: 40px !important;
}

.mr_6 {
  margin-right: 48px !important;
}

.mt_1 {
  margin-top: 8px !important;
}

.mt_2 {
  margin-top: 16px !important;
}

.mt_3 {
  margin-top: 24px !important;
}

.mt_4 {
  margin-top: 32px !important;
}

.mt_5 {
  margin-top: 40px !important;
}

.fw_400 {
  font-weight: 400 !important;
}

.fw_500 {
  font-weight: 500 !important;
}

.fw_600 {
  font-weight: 600 !important;
}

.fw_700 {
  font-weight: 700 !important;
}
/* if width is less than 500px then change challenge card */
@media (max-width: 400px) {
  .challenge_card {
    width: calc(100% - 52px);
    height: 200px;
  }
  .grid_container {
    width: 100%;
    display: flex;
    flex-direction: column;
  }

  .lg_w {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
}
